.heberger {
    width:100%;
    display:flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    position:relative;
    top:30px;
}
.nameHeberger {
    width:40%;
    text-align: right;
    font-family: 'Montserrat';
    font-size:18px;
    font-weight:500;
    line-height: 20px;
    color:#FF6060;
    position:relative;
    right:2%;
}
.photoHeberger {
    display: flex;
    justify-content: flex-end;
    width:64px;
    height:64px;
}
.photoProfil {
    width:100%;
    height:100%;
    object-fit: cover;
    border-radius:50%;
}

@media screen and (max-width:768px) {
    .heberger {
    top:0px;
    width:50%;
    }
    .nameHeberger {
    font-size:12px;
    line-height:17px;
    right:4%;
    }
}